'use client';

import { useState, useEffect } from 'react';

interface Insight {
  id: number;
  title: string;
  insight: string;
  severity: 'low' | 'medium' | 'high' | 'critical';
  tags: string[];
  created_at: string;
}

export default function InsightBox() {
  const [insights, setInsights] = useState<Insight[]>([]);
  const [loading, setLoading] = useState(true);

  // Mock data for local development
  const mockInsights: Insight[] = [
    {
      id: 1,
      title: 'Server Load Alert',
      insight: 'CPU usage has increased 45% over the past hour, indicating potential performance bottleneck.',
      severity: 'high',
      tags: ['performance', 'infrastructure'],
      created_at: new Date().toISOString()
    },
    {
      id: 2,
      title: 'User Engagement Drop',
      insight: 'Daily active users down 12% compared to last week - investigate recent feature changes.',
      severity: 'medium',
      tags: ['analytics', 'user-experience'],
      created_at: new Date().toISOString()
    },
    {
      id: 3,
      title: 'Security Anomaly',
      insight: 'Unusual login patterns detected from 3 new geographic locations in the past 24h.',
      severity: 'critical',
      tags: ['security', 'authentication'],
      created_at: new Date().toISOString()
    }
  ];

  useEffect(() => {
    // Simulate loading
    setTimeout(() => {
      setInsights(mockInsights);
      setLoading(false);
    }, 1000);
  }, []);

  const handleRefresh = () => {
    setLoading(true);
    setTimeout(() => {
      setInsights([...mockInsights]); // Refresh with same data for now
      setLoading(false);
    }, 500);
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-500 to-purple-600 flex items-center justify-center">
        <div className="text-center text-white">
          <h1 className="text-6xl font-bold mb-4">üç≥ InsightBox</h1>
          <p className="text-xl mb-8">Loading insights...</p>
          <div className="flex justify-center space-x-2">
            <span className="w-2 h-2 bg-white rounded-full animate-bounce"></span>
            <span className="w-2 h-2 bg-white rounded-full animate-bounce" style={{animationDelay: '0.1s'}}></span>
            <span className="w-2 h-2 bg-white rounded-full animate-bounce" style={{animationDelay: '0.2s'}}></span>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <div className="flex items-center">
              <h1 className="text-3xl font-bold text-gray-900">üç≥ InsightBox</h1>
              <span className="ml-3 px-3 py-1 bg-blue-100 text-blue-800 text-sm font-medium rounded-full">
                MasterDev Edition
              </span>
            </div>
            <div className="flex items-center space-x-4">
              <button 
                onClick={handleRefresh}
                disabled={loading}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50"
              >
                {loading ? 'Refreshing...' : 'Refresh Insights'}
              </button>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Blank page content */}
      </main>
    </div>
  );
} 